@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Montserrat", sans-serif;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #222;
}
.byAh {
  color: white;
}
.todoContainer {
}
.complete {
  text-decoration: line-through;
}
.App {
  text-align: center;
  min-width: 600px;
  min-height: 80vh;
  background-color: #333;
  border: 5px solid white;
}
.App h1 {
  color: white;
  padding: 15px;
}
.App input {
  background: transparent;
  border: none;
  border-bottom: 3px solid white;
  color: white;
  padding: 5px;
}
.App button {
  background: transparent;
  border: none;
  border-bottom: 3px solid white;
  color: white;
  padding: 5px;
}
.deleteBtn,
.completeBtn {
  background: transparent;
  border: none;
  border-bottom: 3px solid white;
  color: white;
  padding: 5px;
  margin: 5px;
}
.deleteBtn:hover {
  color: red;
  cursor: pointer;
}
.completeBtn:hover {
  color: green;
  cursor: pointer;
}
.add {
  cursor: pointer;
}
@media (max-width: 500px) {
  .App {
    text-align: center;
    min-width: 400px;
    min-height: 80vh;
    background-color: #333;
    border: 5px solid white;
  }
  .App h1 {
    color: white;
    padding: 15px;
  }
  .App input {
    background: transparent;
    border: none;
    border-bottom: 3px solid white;
    color: white;
    padding: 5px;
  }
  .deleteBtn,
  .completeBtn {
    background: transparent;
    border: none;
    border-bottom: 3px solid white;
    color: white;
    padding: 5px;
    margin: 5px;
  }
}

